import React, { useEffect, useState } from "react";

import "./Produtos.css";

export default function Produtos() {
  const [lstProdutosFiltrados, setlstProdutosFiltrados] = useState([]);
  const [lstProdutos, setlstProdutos] = useState([]);
  const [lstCategorias, setlstCategorias] = useState([]);
  const [isLoading, setLoading] = useState(true);

  //async function getAllProdutos() {
  const getAllProdutos = async () => {
    try {
      const response = await fetch(
        "https://x8ki-letl-twmt.n7.xano.io/api:Px6KpV1W/cardapio_pe_da_serra"
        //"https://x8ki-letl-twmt.n7.xano.io/api:p7GSSb0k/produtos"
      );
      const responseJson = await response.json();

      setlstProdutosFiltrados(responseJson);
      setlstProdutos(responseJson);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const getAllCategorias = async () => {
    try {
      const response = await fetch(
        "https://x8ki-letl-twmt.n7.xano.io/api:xR9a0v67/categorias"
      );
      const responseJson = await response.json();

      setlstCategorias(responseJson);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getAllProdutos();
  }, []);

  useEffect(() => {
    getAllCategorias();
  }, []);

  function inputPesquisaChange(event) {
    let novoArray = lstProdutos.filter(
      (produto) =>
        produto.Nome.toUpperCase().includes(event.target.value.toUpperCase()) ||
        produto.Ingredientes.toUpperCase().includes(
          event.target.value.toUpperCase()
        ) ||
        produto.Categoria.Nome.toUpperCase().includes(
          event.target.value.toUpperCase()
        )
    );

    setlstProdutosFiltrados(novoArray);
  }

  function TituloCategoria(props) {
    const arr = lstProdutosFiltrados.filter(
      (produto) => produto.Categoria.id === props.categoria.id
    );

    if (arr.length > 0) {
      return <h3 class="titulo-categoria">{props.categoria.Nome}</h3>;
    } else {
      return "";
    }
  }

  return (
    <>
      <div class="containerInput">
        <input
          class="inputPesquisa"
          placeholder="Pesquisar"
          type="search"
          onChange={inputPesquisaChange}
        />
      </div>

      {lstCategorias.map((categoria) => (
        <>
          <TituloCategoria categoria={categoria} />

          {lstProdutosFiltrados
            .filter((produto) => produto.Categoria.id === categoria.id)
            .map((produto) => (
              <div class="card-item">
                <div class="div-imagem">
                  <img
                    className="imagemProduto"
                    alt="imagem do produto"
                    src={produto.Imagem.url}
                  />
                </div>

                <div className="coluna">
                  <div className="produtoNome">{produto.Nome}</div>
                  <div className="produtoIngredientes">
                    {produto.Ingredientes}
                  </div>
                  <div className="linhaPreco">
                    <div className="preco">R$ {produto.Preco}</div>
                  </div>
                </div>
              </div>
            ))}
        </>
      ))}
      <p className="mensagem-imagens">As imagens são meramente ilustrativas</p>
    </>
  );
}
